.modal-contenedor {
    position: fixed;
    top: 0;
    width: 80%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .5s;
    z-index: 1;
    visibility: hidden;
}


#boton-carrito{
    padding: 0;
    height: 0;
    color: white;
    border: none;
}

.menuNav > button{
    margin-top: 5px;
