.serviciosCard{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    align-items: center;
    margin-left: -4rem;
}

.divdetailserv > h5{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* width: 200px; */
}

.divdetailserv > img{
    display: flex;
    justify-content: center;
}

.textServicios{
    display: flex;
    flex-direction: column;
    margin-left: 10rem;
}

.tituloServicios{
    color: white;
    font-family: Montserrat;
    margin-bottom: 3rem;
    letter-spacing: 10px;
    width: 50rem;
    height: 5rem;
    background-color: black;
    text-shadow: red 0 0, cyan 0 0;
    transition: text-shadow 200ms;
}

.tituloServicios > h1{
    font-weight: bold;
}

.tituloServicios:hover{
    text-shadow: red -8px 0, cyan 6px 0;
}
.allContainerChat{
    width: 25rem;
    height: 30rem;
}


.botonAyuda{
    padding: 1rem;
}

.botonAyuda > button{
    float: right;
    padding: 1rem;
    font-weight: bold;
    border: none;

}

.containerChat{
    display: flex;
    justify-content: center;
}

.botonAyuda > button > img{
    width: 7rem;
    margin-right: -3rem;
}
.texto-quienes{
    width: 50rem;
    margin-left: 16rem;
}

.tituloQuienes{
    background-color: black;
    padding: 1rem;
    margin-bottom: 2rem;
    font-weight: bold;
    text-shadow: rgb(255, 145, 0) 0 0, cyan 0 0;
    transition: text-shadow 200ms;
    letter-spacing: 8px;
    
}

.tituloQuienes:hover{
    text-shadow: rgb(255, 145, 0) -8px 0, cyan 6px 0;
}
.imgHelp > button{
    position: fixed;
    right: 1rem;
    bottom: 1rem;
    background-color: transparent;
    border: none;
}

.imgHelp > button > img {
    width: 4rem;
}

.containerTienda{
    color: white;
    font-family: Montserrat;
    margin-bottom: 20px;
    letter-spacing: 10px;
    width: 50rem;
    height: 5rem;
    background-color: black;
    text-shadow: rgb(255, 145, 0) 0 0, cyan 0 0;
    transition: text-shadow 200ms;
}

.containerTienda > h1{
    font-weight: bold;
}

.containerTienda:hover{
    text-shadow: rgb(255, 145, 0) -10px 0, cyan 6px 0;
}

.cardsTienda{
    display: grid;
    grid-auto-rows: 35rem;
    grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-top: 50px;
}

.LinkDetail{
    text-decoration: none;
    color: white;
}

.navbarTienda{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.botonVolver{
    display: flex;
    justify-content: center;
    margin: 20px;
}

.LinkVolver{
    text-decoration: none;
}

.LinkDetail{
    color: white;
}

.LinkDetail:hover{
    color: rgb(82, 82, 82);
}

.LinkDetail > button{
    background-color: transparent;
    color: white;
    border: none;
    margin-bottom: 2rem;
}

.buttonComprar{
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
}

.buttonComprar > button {
    padding: 1.2rem;
}

.searchbar{
    width: 20rem;
}

.botonOrdenar{
    background-color: black;
    color: white;
    font-weight: lighter;
}

.allCarrito{
    display: none;
}

.botonCarrito{
    background-color: transparent;
    border: none;
    width: 1rem;
}

.divCarrito{
    display: flex;
    justify-content: center;
    margin-right: 5rem;
}

.buttonup > a{
    position: fixed;
    right: 1rem;
    bottom: 1rem;
    background-color: transparent;
    border: none;
}

.buttonup > img{
    width: 200px
}
.containerImg{
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

img{
    height: auto;
    width: auto;
    max-height: 300px;
    max-width: 300px;
    margin-bottom: 20px;
    margin-left: 11rem;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
}

.containerText{
    display: flex;
    flex-direction: column;
    justify-items: center;
    margin-bottom: -2rem;
}

.form-control{
    height: 30px;
}

.btn{
    height: 30px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
}

.botonesCarrito{
    display: flex;
    justify-content: center;
    grid-gap: .7rem;
    gap: .7rem;
    margin-top: 2.4rem;
    align-items: center;
}

.menos{
    width: 1.8rem;
}
.formReserva{
    width: 500px;
    margin-left: 400px;
    margin-top: 100px;
}

.add{
    width: 100px;
}


.leaflet-container{
    width: 50vh;
    height: 50vh;
}
.containerDetail{
    display: grid;
    place-items: center;
    margin-top: 2rem;
    text-transform: capitalize;
}

.containerDetail > img {
    margin-right: 11rem;
}

.containerDetail > button{
    background-color: transparent;
    color: white;
    padding: 20px;
    border: 1px solid rgb(4, 117, 13);
}

.containerDetail > button:hover{
    background-color: rgb(13, 100, 6);
}

.divDesc{
    display: flex;
    justify-content: center;
    margin-left: 26rem;
    width: 500px;
}
.main{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  /* justify-items: stretch !important;  */
  align-content: center;
  background-image: url(/static/media/bg-login.e7f759a8.avif);
  width: 100vw;
  height: 100vh;
}

.main > div {
  width: 500px;
}

.main .col-login{
  background-color: rgba(255, 172, 42, .50);
  border-radius: 7px 0 0 7px;
  justify-self: end;
  padding: 25px;
}

.main .col-register{
  background-color: rgba(0,0,0,.70);
  border-radius: 0 7px 7px 0;
  padding: 25px;
}


/* media queries */
@media screen and (max-width: 650px){
  .main{
    display: grid;
    grid-template-columns: 1fr;
    /* grid-template-rows: 2fr; */
  }
}
